class Title extends React.Component{
    render(){
        return(
            <div>
                <h1>{this.props.firstName} {this.props.lastName}</h1>
                <h4>{this.props.title[0]} , {this.props.title[1]}</h4>
            </div>
        );
    }
}

class Profile extends React.Component{
    //features passing props
    render(){
        return(
            <div className='profile'>
                <img src={profileSVG} alt="profile" />
                <div>
                    <Title firstName={name.firstName} lastName={name.lastName} title={name.title} />
                </div>
            </div>
        );
    }
}
class Clock extends React.Component{
    //features state changes
    constructor(props){
        super(props);
        this.state = ({time: new Date()});
    }
    componentDidMount() {
        setInterval(this.update, 1000);
    }
    update = () => {
        this.setState({time: new Date})
    };
    render(){
        const h = this.state.time.getHours();
        const m = this.state.time.getMinutes();
        const s = this.state.time.getSeconds();
        return(
            <div className="clock">
                <h2>{h % 12}:{(m < 10 ? '0' + m : m)}:{(s < 10 ? '0' + s : s)} {h < 12 ? 'am' : 'pm'}</h2>
            </div>
        );
    }
}

